.zigzag {
  position: relative;
  height: 8px;
  width: 80px;
  margin: 0 auto;
  z-index: 1;
}

.zigzag:before, .zigzag:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  background-position: top left;
  background-repeat: repeat-x;
  background-size: 10px 10px;
}

.zigzag:before {
  height: 10px;
  top: 117%;
  background-image: linear-gradient(-135deg, #1d2c40 5px, transparent 0px), linear-gradient(135deg, #1d2c40 5px, transparent 0px);
}

.zigzag:after {
  height: 12px;
  top: 100%;
  background-image: linear-gradient(-135deg, #f0ebe7 5px, transparent 0px), linear-gradient(135deg, #f0ebe7 5px, transparent 0px);
}

.zigzag--dark {
  margin-left: 0;
}

.zigzag--dark:before {
  background-image: linear-gradient(-135deg, #1ce4c6 5px, transparent 0px), linear-gradient(135deg, #1ce4c6 5px, transparent 0px);
}

.zigzag--dark:after {
  background-image: linear-gradient(-135deg, #0c283f 5px, transparent 0px), linear-gradient(135deg, #0c283f 5px, transparent 0px);
}

.zigzag--light:before {
  background-image: linear-gradient(-135deg, #bbbbbb 5px, transparent 0px), linear-gradient(135deg, #bbbbbb 5px, transparent 0px);
}

.zigzag--light:after {
  background-image: linear-gradient(-135deg, #f0ebe7 5px, transparent 0px), linear-gradient(135deg, #f0ebe7 5px, transparent 0px);
}

