  line-height: 1.2;
  color: #ffffff;
  text-decoration: none;
}

@media only screen and (max-width: 599px) {
  .footer__copyriting {
    margin-bottom: 20px;
  }
}

.section-form {
  padding-bottom: 105px;
  color: #fff;
  background-color: #0c283f;
}

@media only screen and (max-width: 599px) {
  .section-form {
    padding: 0 0 40px 0;
  }
}

.section-form__wrap {
  padding: 0 17px;
}

.section-form__title {
  margin-bottom: 11px;
}

.form {
  letter-spacing: 0.3px;
}

.form__item {
  display: flex;
  margin-bottom: 22px;
  box-sizing: border-box;
  background: #ffffff;
  font-weight: normal;
  font-size: 13px;
  text-transform: uppercase;
  line-height: 1.2;
  color: #333;
  border-radius: 3px;
}

@media only screen and (max-width: 767px) {
  .form__item {
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 767px) {
  .form__item--wrap {
    background: none;
    border-radius: 0;
  }
}

.form__item--textarea {
  margin-bottom: 27px;
}

.form__label {
  position: relative;
  width: 33.33%;
  padding: 21px 0;
}

.form__label:not(:last-child)::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 1px;
  height: 35px;
  background: #86939d;
}

@media only screen and (max-width: 767px) {
  .form__label {
    margin-bottom: 15px;
    background: #ffffff;
    border-radius: 3px;
  }
}

.form__label--name {
  width: 31%;
}

@media only screen and (max-width: 1168px) {
  .form__label--name {
    width: 100%;
  }
}

.form__label--company {
  width: 30%;
}

@media only screen and (max-width: 1168px) {
  .form__label--company {
    width: 100%;
  }
}

.form__label--email {
  width: 39%;
}

@media only screen and (max-width: 1168px) {
  .form__label--email {
    width: 100%;
  }
}

.form__input {
  position: relative;
  width: 100%;
  padding: 0 24px;
  font-size: 16px;
  line-height: 1.25;
  color: #383e4a;
}

.form__textarea {
  display: block;
  width: 100%;
  padding: 28px 21px 25px;
  font-size: 16px;
  line-height: 1.25;
  color: #383e4a;
  background-color: transparent;
  border: none;
  resize: none;
  overflow: auto;
}

.form__footer {
  display: flex;
  flex-direction: column;
}

.form__submit {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
}

@media only screen and (max-width: 599px) {
  .form__submit {
    flex-direction: column;
    align-items: flex-start;
  }
}

.form__btn {
  margin-right: 14px;
}

@media only screen and (max-width: 1168px) {
  .form__btn {
    margin-right: 0;
  }
}

.form__info {
  font-size: 18px;
  line-height: 1.44;
  color: #ffffff;
}

@media only screen and (max-width: 1168px) {
  .form__info {
    font-size: 14px;
    line-height: 1.2;
    order: 1;
  }
}

.form__check {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 9px;
  user-select: none;
  cursor: pointer;
}

.form__check:hover .form__check-btn {
  border-color: #06dbc4;
}

@media only screen and (max-width: 1168px) {
  .form__check {
    margin-bottom: 12px;
  }
}

.form__check-input {
  display: none;
}

.form__check-input:checked + .form__check-btn {
  background-image: url(../img/ico/check.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 11px 7px;
}

.form__check-btn {
  display: inline-block;
  width: 20px;
  height: 19px;
  margin-right: 10px;
  border: 2px solid #ffffff;
  border-radius: 1px;
  transition: border-color .3s ease-out;
}

.form__check-text {
  text-decoration: underline;
}

.social-list {
  display: flex;
}

.social-list__item:not(:last-child) {
  margin-right: 5px;
}

.social-btn {
  position: relative;
  display: block;
  width: 39px;
  height: 39px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-image: url(../img/ico/fb.jpg);
}

.social-btn:hover {
  opacity: 0.8;
}

.social-btn--fb {
  background-image: url(../img/ico/fb.jpg);
}

.social-btn--tw {
  background-image: url(../img/ico/twit.jpg);
}

.social-btn--in {
  background-image: url(../img/ico/in.jpg);
}
