@include font('helvetic-font', helveticaneue);
@include font-param('helvetic-font', helvetic_neue_bold, bold);

@include font('myriadpro-font', myriadpro-regular);
@include font-param('myriadpro-font', myriadpro-bold, bold);

@include font('opensans-font', opensans-regular);
@include font-param('opensans-font', opensans-bold, bold);

@mixin helvetic-font {
	font-family: helvetic-font, sans-serif; }

@mixin myriadpro-font {
	font-family: myriadpro-font, sans-serif; }

@mixin opensans-font {
	font-family: opensans-font, sans-serif; }

body {
	@include myriadpro-font;
	background-color: $bg-color; }

.container {
	max-width: 1168px;
	margin: 0 auto; }

//form//
button,
input,
optgroup,
select,
textarea {
	padding: 0;
	margin: 0;
	border: none;
	box-shadow: none;
	background-color: transparent;
	border-radius: 0;
	font-family: inherit;
	color: inherit;
	font-size: inherit;
	line-height: inherit;
	letter-spacing: inherit;
	font-weight: inherit;
	-webkit-appearance: none; }


@include placeholder {
	font-size: 16px;
	line-height: 1.25;
	color: #848484; }


@keyframes dash {
	from {
		stroke-dashoffset: 2000; }

	to {
		stroke-dashoffset: 0;
 } }		// fill: currentColor

